* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.login-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.error-notification {
  border: 2px solid mediumslateblue;
  width: 350px;
  position: absolute;
  top: -300px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
}
.error-notification.show {
  top: 100px;
  transition: top ease-in-out 0.2s;
}
@media screen and (max-height: 1000px) {
  .error-notification.show {
    top: 100px;
  }
}
@media screen and (max-height: 850px) {
  .error-notification.show {
    top: 50px;
  }
}
@media screen and (max-height: 750px) {
  .error-notification.show {
    top: 20px;
  }
}
@media screen and (max-height: 650px) {
  .error-notification.show {
    top: 0px;
  }
}
.error-notification {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  background: #fff;
}
.error-notification p {
  font-size: 1.2rem;
  color: red;
  text-transform: capitalize;
}
.error-notification button {
  position: absolute;
  right: 2%;
  top: 5%;
  background: none;
  border: 0;
  padding: 2px;
  cursor: pointer;
}
.error-notification button:hover {
  transform: scale(1.2);
  transition: transform 0.1s ease-in;
}
.login-container form {
  display: flex;
  flex-direction: column;
  padding: 40px;
  width: 350px;
  border-radius: 8px;
  background: #fff;
  box-shadow: 0 0 2px 4px #cacaca;
  position: relative;
}

.login-container form .heading-sec h1 {
  text-align: center;
  font-size:1.9rem;
  font-weight: 600;
  letter-spacing: 1px;
}

.login-container form input {
  font-size: 1.1rem;
  padding: 10px;
  height:50px;
  border: 2px solid mediumslateblue;
  border-radius: 4px;
}

.login-container form label {
  display: flex;
  position: relative;
}
.login-container form label input {
  width: 100%;
}
.login-container form label button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-container form label button {
  border: 0;
  background: none;
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
}
.login-container form label button:hover {
  background: #cacacab4;
  transition: ease-in 0.1s;
}
.login-container form label button img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.login-container form input[type="text"] {
  font-size: 1rem;
  color: black;
}
.login-container form input::placeholder {
  font-size: 1rem;
}
.login-container form input:focus {
  outline: 2px solid mediumslateblue;
}

.login-container form p {
  margin-top: 30px;
  margin-bottom: 2px;
  color: red;
  font-size: 1.1rem;
}
.login-container form .form-footer-sec {
  margin-top: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login-container form .form-footer-sec p {
  margin-top: 0;
  color: mediumslateblue;
}

.login-container form .form-footer-sec button {
  padding: 14px 25px;
  font-size: 1rem;
  letter-spacing: 0.5px;
  font-weight: 600;
  background-color: mediumslateblue;
  border: 0;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}

.login-container form .form-footer-sec button:hover {
  opacity: 0.8;
  transition: opacity ease-in 0.1s;
}
